import {Injectable} from "@angular/core";
import * as CryptoJS from 'crypto-js';
import {WalletService} from "./wallet.service";
import {ModalController, ToastController} from "ionic-angular";
import {ArticleService} from "./article.service";
import {UserService} from "./user.service";

@Injectable()
export class RequestService {
  private key: string = 'vfd54788efz5fg15er((efeg15eegr15';

  constructor(private articleService: ArticleService,
              public toastCtrl:ToastController,
              private walletService: WalletService,
              public modalCtrl: ModalController,
              private userService: UserService) {

  }

  generateRequest(type, content) {
    let request = {};
    request['type'] = type;
    request['content'] = content;
    var encrypted = CryptoJS.AES.encrypt(JSON.stringify(request), this.key);
    var encryptedmessage = 'neoplace' + encrypted.toString();
    return encryptedmessage;
  }

  processRequest(requestContent) {
    let content = requestContent.replace('neoplace', '');
    var decrypted = CryptoJS.AES.decrypt(content, this.key);
    var c = decrypted.toString(CryptoJS.enc.Utf8);
    let request = JSON.parse(c);

    if(request.type) {
      switch(request.type) {
        case "pay": {
          let wallets = this.walletService.wallets;
          let modal = this.modalCtrl.create('ModalWalletAction', {
            wallet: {trigram: request.content.trigram.toLowerCase(), address: wallets[request.content.trigram.toLowerCase()].address, private: wallets[request.content.trigram.toLowerCase()].private},
            amount: request.content.amount,
            address: request.content.fromAddress
          });
          modal.present();
          break;
        }
        case "pay-fiat": {
          let wallets = this.walletService.wallets;
          let modal = this.modalCtrl.create('ModalWalletAction', {
            wallet: {trigram: request.content.trigram.toLowerCase(), address: wallets[request.content.trigram.toLowerCase()].address, private: wallets[request.content.trigram.toLowerCase()].private},
            amount: request.content.amount,
            address: request.content.fromAddress,
            fiat: true,
            amountFiat: request.content.amountFiat,
            currency: request.content.currency
          });
          modal.present();
          break;
        }
        case "pay-article": {
          //TODO change trigram, ask which trigram
          let isPass = false;
          this.articleService.getArticleFromIdPublished(request.content.id).subscribe(article => {
            if(!isPass) {
              if(article) {
                let wallets = this.walletService.wallets;
                let crypto = this.userService._profil.paymentMethod.toUpperCase();
                let modal = this.modalCtrl.create('ModalArticleAction', {
                  wallet: {trigram: crypto, address: wallets[crypto.toLowerCase()].address, private: wallets[crypto.toLowerCase()].private},
                  trigram: crypto,
                  article: article,
                  id: request.content.id
                });
                modal.present();

              } else {
                let toast = this.toastCtrl.create({
                  message: 'Error transaction',
                  duration: 2000,
                  position: 'bottom'
                });

                toast.present(toast);
              }
              isPass = true;
            }
          });
          break;
        }
        case "pay-inventory": {
          //TODO change trigram, ask which trigram
          let isPass = false;
          this.articleService.getInventoryWithUid(request.content.uid, request.content.id).subscribe(article => {
            if(!isPass) {
              if(article) {
                let wallets = this.walletService.wallets;
                let crypto = this.userService._profil.paymentMethod.toUpperCase();
                let modal = this.modalCtrl.create('ModalArticleAction', {
                  wallet: {trigram: crypto, address: wallets[crypto.toLowerCase()].address, private: wallets[crypto.toLowerCase()].private},
                  trigram: crypto,
                  article: article,
                  id: request.content.id,
                  isInventory: true
                });
                modal.present();
              } else {
                let toast = this.toastCtrl.create({
                  message: 'Error transaction',
                  duration: 2000,
                  position: 'bottom'
                });

                toast.present(toast);
              }
              isPass = true;
            }
          });
          break;
        }
        case "pay-service": {
          //TODO change trigram, ask which trigram
          let isPass = false;
          this.articleService.getServiceOrderId(request.content.id).subscribe(service => {
            if(!isPass) {
              if(service) {
                let wallets = this.walletService.wallets;
                let crypto = this.userService._profil.paymentMethod.toUpperCase();
                let modal = this.modalCtrl.create('ModalServiceAction', {
                  wallet: {trigram: crypto, address: wallets[crypto.toLowerCase()].address, private: wallets[crypto.toLowerCase()].private},
                  trigram: crypto,
                  serviceOrdered: service,
                  id: request.content.id
                });
                modal.present();
              } else {
                let toast = this.toastCtrl.create({
                  message: 'Error transaction',
                  duration: 2000,
                  position: 'bottom'
                });

                toast.present(toast);
              }
              isPass = true;
            }
          });
          break;
        }
        case "order-service": {
          //TODO change trigram, ask which trigram
          let isPass = false;
          this.articleService.getServiceFromId(request.content.id).subscribe(service => {
            if(!isPass) {
              if(service) {
                let wallets = this.walletService.wallets;
                let crypto = this.userService._profil.paymentMethod.toUpperCase();
                let modal = this.modalCtrl.create('ModalServiceAction', {
                  wallet: {trigram: crypto, address: wallets[crypto.toLowerCase()].address, private: wallets[crypto.toLowerCase()].private},
                  trigram: crypto,
                  service: service,
                  id: request.content.id
                });
                modal.present();
              } else {
                let toast = this.toastCtrl.create({
                  message: 'Error transaction',
                  duration: 2000,
                  position: 'bottom'
                });

                toast.present(toast);
              }
              isPass = true;
            }
          });
          break;
        }

      }
    }

  }
}



