import {Injectable} from '@angular/core';
import 'rxjs/add/operator/map';
import {GoogleMap, GoogleMaps, GoogleMapsEvent, GoogleMapOptions} from "@ionic-native/google-maps";

/*
  Generated class for the MapServiceProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class MapServiceProvider {

  map: GoogleMap;
  markersArticle = [];
  markersService = [];
  markersFiat = [];

  constructor(
    private googleMaps: GoogleMaps
  ) {
    //console.log('Hello MapServiceProvider Provider');
  }

  setDiv(el: HTMLElement, mapOptions: GoogleMapOptions): Promise<any> {
    return new Promise<any>((resolve) => {
      if (!this.map && el != null) {
        this.map = GoogleMaps.create(el, mapOptions);
        this.map.one(GoogleMapsEvent.MAP_READY).then(() => {
          resolve();
        });
      } else {
        this.map.clear();
        this.map.setDiv(el);
        resolve();
      }
    });
  }

  hideArticles() {
    this.markersArticle.forEach(marker => marker.marker.setVisible(false));
  }

  displayArticles() {
    this.markersArticle.forEach(marker => marker.marker.setVisible(true));
  }

  hideServices() {
    this.markersService.forEach(marker => marker.setVisible(false));
  }

  displayServices() {
    this.markersService.forEach(marker => marker.setVisible(true));
  }

  hideFiats() {
    this.markersFiat.forEach(marker => marker.setVisible(false));
  }

  displayFiats() {
    this.markersFiat.forEach(marker => marker.setVisible(true));
  }

  findArticle(text1: string) {
    this.markersArticle.forEach(marker => {
      if(text1 && text1.trim() != '') {
        let text = text1.toLowerCase();

        if(marker.article.title.toLowerCase().indexOf(text) >= 0) {
          marker.marker.setVisible(true);
        } else if(marker.article.type.toLowerCase().indexOf(text) >= 0) {
          marker.marker.setVisible(true);
        } else if(marker.article.description.toLowerCase().indexOf(text) >= 0) {
          marker.marker.setVisible(true);
        } else if(marker.article.brand.toLowerCase().indexOf(text) >= 0) {
          marker.marker.setVisible(true);
        } else {
          marker.marker.setVisible(false);
        }
      } else {
        marker.marker.setVisible(true);
      }
    });
  }

  findService(text1: string) {
    this.markersService.forEach(marker => {
      if(text1 && text1.trim() != '') {
        let text = text1.toLowerCase();

        if(marker.service.title.toLowerCase().indexOf(text) >= 0) {
          marker.marker.setVisible(true);
        } else if(marker.service.type.toLowerCase().indexOf(text) >= 0) {
          marker.marker.setVisible(true);
        } else if(marker.service.description.toLowerCase().indexOf(text) >= 0) {
          marker.marker.setVisible(true);
        } else {
          marker.marker.setVisible(false);
        }
      } else {
        marker.marker.setVisible(true);
      }
    });
  }



}



