import {Injectable} from '@angular/core';
import {Api} from './api';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';
import {Profil} from "./model/profil";
import {AngularFireAuth} from "angularfire2/auth";
import {AngularFireDatabase} from "angularfire2/database";
import {User} from "./model/user";
import {WalletService} from "./wallet.service";
import {Observable} from "rxjs/Rx";
import {AlertController} from "ionic-angular";
import {AppState} from "../app/global.setting";
import * as CryptoJS from 'crypto-js';
import {FirebaseProvider} from "./firebase/firebase";


@Injectable()
export class UserService {
  public _userFirebase;
  public _user: User;
  public _profil: Profil;



  constructor(public alertCtrl: AlertController,
              private walletService: WalletService,
              private afd: AngularFireDatabase,
              private afAuth: AngularFireAuth,
              public global: AppState,
              private firebase: FirebaseProvider) {
  }

  login(accountInfo: any) {
    let seq = this.afAuth.auth.signInWithEmailAndPassword(accountInfo.username, accountInfo.password);

    seq.then(auth => {
      if(auth) {
        this._userFirebase = auth;
        this._user = <User>({
          username: this._userFirebase.email
        });
        auth.getIdToken().then(token => this._user.token = "Bearer " + token);
        // this.getProfil(auth.uid).subscribe(p => {
        // this._profil = p;
        // this.global.set('profil', p)});
        this.walletService.getWallets(auth.uid).subscribe(w => this.walletService.wallets = w);
      }
    }).catch(err => {

    });
    return seq;
  }


  signup(accountInfo: any) {
    let seq = this.afAuth.auth.createUserWithEmailAndPassword(accountInfo.username, accountInfo.password);

    seq.then(userFire => {
      this._userFirebase = userFire;
      let obs = [];
      //call backend wallet creation
      for (let trigram of this.walletService.trigramAvailable) {
        obs.push(this.walletService.createWallet(trigram));
      }
      //wait to receive all response
      Observable.forkJoin(obs).subscribe(wallets => {
        let i = 0;
        let contentWallets = {};
        for (let trigram of this.walletService.trigramAvailable) {
          contentWallets[trigram.toLowerCase()] = {};
          contentWallets[trigram.toLowerCase()]['address'] = wallets[i]['address'];
          contentWallets[trigram.toLowerCase()]['public'] = wallets[i]['public'];
          contentWallets[trigram.toLowerCase()]['private'] = CryptoJS.AES.encrypt(wallets[i]['private'], accountInfo.password).toString(); //to encrypt
          i++;
        }
        this.walletService.saveWallets(contentWallets, userFire.uid);
        this.walletService.wallets = contentWallets;
      });


    }).catch(err => {

    });

    return seq;
  }

  checkAuthentication(){
    let seq = this.afAuth.authState;

    seq.subscribe(auth => {
      if(auth) {
        this._userFirebase = auth;
        this._user = <User>({
          username: this._userFirebase.email
        });
        auth.getToken().then(token => this._user.token = "Bearer " + token);
        // this.getProfil(auth.uid).subscribe(p =>{
        //   this._profil = p;
        //   this.global.set('profil', p);
        // });
        this.walletService.getWallets(auth.uid).subscribe(w => this.walletService.wallets = w);
      }
    });
    return seq;

  }

  /**
   * Log the user out, which forgets the session
   */
  logout() {
    //console.log("ici");
    this._user = null;
    this._userFirebase = null;
    this._profil = null;
    this.afAuth.auth.signOut().then(resp => {
      const alert1 = this.alertCtrl.create({
        title: 'Disconnected',
        subTitle: 'You have been sucessfully log out',
        buttons: ['Close']
      });
      alert1.present();
    })
  }

  saveProfil(profil) {
    delete profil.$value;
    if(profil.imaUrl && profil.imageUrl.indexOf("http") == -1) {
      this.firebase.uploadToFirebaseData(profil.imageUrl, "profil").then((value:any) => {
        profil.imaUrl = value.photo;
        this.afd.object('/users/' + this._userFirebase.uid + "/profil/").set(profil);
        this.afd.object('/pseudos/' + profil.pseudo).set(this._userFirebase.uid);
      });
    } else {
      this.afd.object('/users/' + this._userFirebase.uid + "/profil/").set(profil);
      this.afd.object('/pseudos/' + profil.pseudo).set(this._userFirebase.uid);
    }
  }

  getProfil(uid) {
    return this.afd.object('/users/' + uid + '/profil/');
  }

  getUidFromPseudo(pseudo: string) {
    return this.afd.object('/pseudos/' + pseudo);
  }

  saveProfilPosition(lat, lng) {
    this.afd.object('/positions/' + this._userFirebase.uid).set({lat: lat, lng: lng});
  }

  deleteProfilPosition() {
    this.afd.object('/positions/' + this._userFirebase.uid).remove().then();
  }

  getPositions() {
    return this.afd.list('/positions/');
  }
}



