import {Injectable} from "@angular/core";
import {Api} from "./api";
import {AlertController, ToastController} from "ionic-angular";
import {Transaction} from "./model/transaction";
import {UserService} from "./user.service";
import {AngularFireDatabase, FirebaseListObservable} from "angularfire2/database";
import {ArticleService} from "./article.service";
import {Wallet} from "./model/wallet";
import * as CryptoJS from 'crypto-js';
import * as Bitcoin from 'bitcoinjs-lib';
import * as bigi from 'bigi';
import * as buffer from 'buffer';
import {Observable} from "rxjs/Observable";

declare const Buffer;

@Injectable()
export class TransactionService {

  token = "4dcf9608e8ad4fd8bb3e5262b66d4cee";

  private walletCaract = {
    btc : {url : "btc/test3", rate: Math.pow(10, 8)},
    eth : {url : "beth/test", rate: Math.pow(10, 18)}
  };

  tokenUrl = "?token=" + this.token;

  transactions: Transaction[];

  constructor(public toastCtrl:ToastController,
              private api: Api,
              private userService: UserService,
              private articleService: ArticleService,
              private afd: AngularFireDatabase,
              public alertCtrl: AlertController) {

  }

  private sendCrypto(trigram: string, amount: number, wallet: Wallet, toAddress: string, password: string) {
    amount = Math.round(amount * this.walletCaract[trigram.toLowerCase()].rate);
    //console.log(wallet);
    //console.log(password);
    try {
      let decrypted = CryptoJS.AES.decrypt(wallet.private, password);
      let key = decrypted.toString(CryptoJS.enc.Utf8);
      var keys = new Bitcoin.ECPair(bigi.fromHex(key));
    } catch (e){
      return Observable.throw(new EvalError('Your password is not correct'));
    }

      let request = this.api.post(
        this.walletCaract[trigram.toLowerCase()].url + "/txs/new" + this.tokenUrl,
        JSON.stringify({
          inputs: [{addresses: [wallet.address]}],
          "outputs": [{"addresses": [toAddress], "value": amount}]
        }));

      let self = this;

      request
        .subscribe(response => {
          let content = response.json();

          content.signatures = [];
          content.pubkeys = [];
          for (let s of content.tosign) {
            content.signatures.push(keys.sign(new buffer.Buffer(s, "hex")).toDER().toString("hex"));
            content.pubkeys.push(keys.getPublicKeyBuffer().toString("hex"));
          }
          self.api.post(
            this.walletCaract[trigram.toLowerCase()].url + "/txs/send" + this.tokenUrl,
            JSON.stringify(content)
          ).subscribe(response1 => {
          });
        });
      return request;
  }

  send(trigram: string, wallet: Wallet, toAddress: string, amount: number, password: string, label: string) {

    this.sendCrypto(trigram, amount, wallet, toAddress, password)
      .subscribe(res => {
      this.saveTransaction(
        <Transaction>({
          cryptoTrigram: trigram,
          walletFrom: wallet.address,
          walletTo: toAddress,
          amount: amount,
          date: Date.now(),
          label: label
        })
      );

      let toast = this.toastCtrl.create({
        message: 'Transaction send',
        duration: 2000,
        position: 'bottom'
      });
      toast.present(toast);
    }, error => {
        let message = "Not enough funds to complete your transaction";
        if(error instanceof EvalError){
          message = "Your password is not correct, please re-enter your password";
        }
        const alert = this.alertCtrl.create({
          title: 'Error !',
          subTitle: message,
          buttons: ['Close']
        });
        alert.present();
      });
  }

  getFiat(trigram: string, wallet: Wallet, toAddress: string, amount: number, password: string, label: string, currency: string) {
    this.sendCrypto(trigram, amount, wallet, toAddress, password)
      .subscribe(res => {
      this.saveTransaction(
        <Transaction>({
          cryptoTrigram: trigram,
          walletFrom: wallet.address,
          walletTo: toAddress,
          amount: amount,
          date: Date.now(),
          label: label,
          fiat: true,
          currency: currency
        })
      );

      let toast = this.toastCtrl.create({
        message: 'Transaction send',
        duration: 2000,
        position: 'bottom'
      });
      toast.present(toast);
    }, error => {
        let message = "Not enough funds to complete your transaction";
        if(error instanceof EvalError){
          message = "Your password is not correct, please re-enter your password";
        }
        const alert = this.alertCtrl.create({
          title: 'Error !',
          subTitle: message,
          buttons: ['Close']
        });
        alert.present();
      });
  }

  payArticle(trigram: string, wallet: Wallet, toAddress: string, amount: number, password: string, label: string, article, articleId) {

    //console.log("pay article", amount);
    this.sendCrypto(trigram, amount, wallet, toAddress, password)
      .subscribe(res => {
      this.saveTransaction(
        <Transaction>({
          cryptoTrigram: trigram,
          walletFrom: wallet.address,
          walletTo: toAddress,
          amount: amount,
          date: Date.now(),
          label: label,
          articleId: articleId
        })
      );

      article.status = "sold";
      article.buyeruid = this.userService._userFirebase.uid;

      this.articleService.updateArticle(articleId, article);

        let toast = this.toastCtrl.create({
          message: 'Transaction saved and funds locked in Smart contract.\nConfirm the reception once the item received to unlock funds',
          duration: 5000,
          position: 'bottom'
        });

        toast.present(toast);
    }, error => {
        let message = "Not enough funds to complete your transaction";
        if(error instanceof EvalError){
          message = "Your password is not correct, please re-enter your password";
        }
        const alert = this.alertCtrl.create({
          title: 'Error !',
          subTitle: message,
          buttons: ['Close']
        });
        alert.present();
      });


  }

  payInventory(trigram: string, wallet: Wallet, toAddress: string, amount: number, password: string, label: string, article) {
    this.sendCrypto(trigram, amount, wallet, toAddress, password)
      .subscribe(res => {
      this.saveTransaction(
        <Transaction>({
          cryptoTrigram: trigram,
          walletFrom: wallet.address,
          walletTo: toAddress,
          amount: amount,
          date: Date.now(),
          label: label,
          inventory: {uid: article.uid, id: article.ean}
        })
      );

      let toast = this.toastCtrl.create({
        message: 'Article paid',
        duration: 2000,
        position: 'bottom'
      });
      toast.present(toast);
    }, error => {
        let message = "Not enough funds to complete your transaction";
        if(error instanceof EvalError){
          message = "Your password is not correct, please re-enter your password";
        }
        const alert = this.alertCtrl.create({
          title: 'Error !',
          subTitle: message,
          buttons: ['Close']
        });
        alert.present();
      });
  }

  payService(trigram: string, wallet: Wallet, toAddress: string, amount: number, password: string, label: string, service, serviceOrdered) {
    this.sendCrypto(trigram, amount, wallet, toAddress, password)
      .subscribe(res => {
        this.saveTransaction(
          <Transaction>({
            cryptoTrigram: trigram,
            walletFrom: wallet.address,
            walletTo: toAddress,
            amount: amount,
            date: Date.now(),
            label: label,
            service: {uid: service.uid, id: service.id}
          })
        );

        serviceOrdered.status = "paid";
        this.articleService.updateServiceOrdered(serviceOrdered.$key, serviceOrdered);

        let toast = this.toastCtrl.create({
          message: 'Service paid',
          duration: 2000,
          position: 'bottom'
        });
        toast.present(toast);
      }, error => {
        let message = "Not enough funds to complete your transaction";
        if(error instanceof EvalError){
          message = "Your password is not correct, please re-enter your password";
        }
        const alert = this.alertCtrl.create({
          title: 'Error !',
          subTitle: message,
          buttons: ['Close']
        });
        alert.present();
      });
  }


  saveTransaction(transaction) {
    this.afd.list('/transactions/').push(transaction);
  }

  getMyTransactions(address): FirebaseListObservable<Transaction[]> {
    return this.afd.list('/transactions/', {
      query: {
        orderByChild: "walletFrom",
        equalTo: address
      }
    });
  }

  getOtherTransaction(address): FirebaseListObservable<Transaction[]> {
    return this.afd.list('/transactions/', {
      query: {
        orderByChild: "walletTo",
        equalTo: address
      }
    });
  }

  getWalletTransaction(trigram: string, address: string) {
    return this.api.get(this.walletCaract[trigram.toLowerCase()].url + "/addrs/" + address,
      {}).map(response => response.json().txrefs);
  }

}



