import {Injectable} from '@angular/core';
import {AngularFireDatabase} from 'angularfire2/database';
import * as firebase from 'firebase';

@Injectable()
export class FirebaseProvider {

  constructor(public afd: AngularFireDatabase) {
    //console.log('Hello FirebaseProvider Provider');
  }

  getNotesDB() {
    return this.afd.list('/notes/');
  }

  saveNotesDB(datas) {

    if (datas.$key) {
      this.afd.list('/notes/').update(datas.$key, datas);
    }
    else {
      this.afd.list('/notes/').push(datas);
    }
  }

  deleteNotesDB(id) {
    this.afd.list('/notes/').remove(id);
  }


  uploadToFirebaseData(data, type)  {

    // Create a uuid as filename
    let filename : string  = uuidv4() + '.jpg';
    //console.log("filename: ", filename);

    return new Promise((resolve, reject) => {
      // Upload file
      let doUpload   = firebase.storage().ref(type + "/" +filename).putString(data,'data_url');
      //console.log("doUpload: ", doUpload);
      doUpload.on('state_changed',
        (snapshot) =>{
          //console.log('snapshot progess ' + snapshot);
          //make progress
          // We could log the progress here IF necessary
          // console.log('snapshot progess ' + snapshot);
        },
        (error) => {
          reject(error);
        },
        () =>
        {   //if complete
          let imageData ={
            photo:doUpload.snapshot.downloadURL,
            photoName:filename
          };
          resolve(imageData);
        }
      );
    });
  }

  uploadToFirebaseFile(file, type)  {

    // Create a uuid as filename
    let filename : string  = uuidv4() + '.jpg';
    //console.log("filename: ", filename)

    return new Promise((resolve, reject) => {
      // Upload file
      let doUpload   = firebase.storage().ref(type + "/" +filename).put(file);
      //console.log("doUpload: ", doUpload);
      doUpload.on('state_changed',
        (snapshot) =>{
          //console.log('snapshot progess ' + snapshot);
          //make progress
          // We could log the progress here IF necessary
          // console.log('snapshot progess ' + snapshot);
        },
        (error) => {
          reject(error);
        },
        () =>
        {   //if complete
          let imageData ={
            photo:doUpload.snapshot.downloadURL,
            photoName:filename
          };
          resolve(imageData);
        }
      );
    });
  }
}

function uuidv4() {
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
    var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
    return v.toString(16);
  });
}



